import React, {Component} from "react";
import {MobileContract} from "eshop/modules/auth/components/MobileContract";

export class MobileBillingAccount extends Component {

    constructor(props) {
        super(props);

    }

    render(){
        return (
            <div className="u-padding-checkbox" >
                <ul className="u-margin-m">
                {
                    this.props.account && this.props.account.accountContracts && this.props.account.accountContracts.length > 0  && this.props.account.accountContracts.map(contract => (
                        <MobileContract key={contract.contractId} data={contract}/>
                    ))
                }
                </ul>
            </div>
        )
   }
}
